%product-content {
  .content {
    margin-top:60px;
    .col-md-4 {
      position:relative;
      z-index:8;
      background-color:#fff;
      &:hover {
        z-index:9
      }
    }
    .more {
      color:#000;
      font-weight: bold;
      margin-bottom:30px;
      margin-right:40px;
      overflow:hidden;
      a {
        border-bottom:1px solid #000;
      }
    }
    .product {
      display:block;
      transition:all .5s ease;
      position:relative;
      border-radius:10px;
      height:280px;
      max-width:330px;
      margin:0 auto 30px auto;
      &:hover {
        transform: translateY(-15px);
      }
      .overlay {
        background-color:#fff;
        z-index:9;
        transition:all .5s ease;
        padding:0 20px 20px 20px;
        border:1px solid #{$border-grey};
        &:hover {
          border:1px solid transparent;
          box-shadow:0 5px 50px #f4f4f4;
          height:calc(100% + 30px);
          
          .options > div.to-wishlist {
            bottom:40px;
          }
        }

        img {
          display:block;
          width:200px;
          margin:auto;
        }

        .options > div {
          position:absolute;
          right:20px;
          color:$mainColor;
          cursor:pointer;
          &.to-cart {
            top:20px;
            &:after {
              content:'';
              position:absolute;
              top:calc(50% - 2px);
              left:calc(50% - 2px);
              width: 4px;
              height: 4px;
              border-radius:50%;
              opacity:0;
              box-shadow: 0.32476rem -3rem 0 -0.1875rem #ff8080,
              -0.32476rem -2.625rem 0 -0.1875rem #000,
                2.54798rem -1.61656rem 0 -0.1875rem #000,
                1.84982rem -1.89057rem 0 -0.1875rem #a4ff80,
                  2.85252rem 0.98418rem 0 -0.1875rem #a4ff80,
                  2.63145rem 0.2675rem 0 -0.1875rem #80ffc8,
                    1.00905rem 2.84381rem 0 -0.1875rem #80ffc8,
                    1.43154rem 2.22414rem 0 -0.1875rem #00022e,
                      -1.59425rem 2.562rem 0 -0.1875rem #3a3a3a,
                      -0.84635rem 2.50595rem 0 -0.1875rem #36013f,
                        -2.99705rem 0.35095rem 0 -0.1875rem #36013f,
                        -2.48692rem 0.90073rem 0 -0.1875rem #d90166,
                          -2.14301rem -2.12438rem 0 -0.1875rem #d90166,
                          -2.25479rem -1.38275rem 0 -0.1875rem #ff8080;
            }
            &.added{
              color:$red;
              &:after {
                animation: sparkles 1s cubic-bezier(0.17, 0.89, 0.32, 1.49);
              }
            }
          }
          &.to-wishlist {
            bottom:20px;
            transition:all .5s ease;
          }
        }
      }
    }

    .custom-pagination {
      margin:auto;
      display:flex;
      justify-content: center;
      a {
        height: 45px;
        width: 45px;
        line-height: 45px;
        display: inline-block;
        margin-top:30px;
        padding: 0;
        font-size: 12px;
        text-align: center;
        color:$mainColor;
      }
      a.num {
        margin:30px 10px 0 10px;
        border:1px solid #{$mainColor};
        border-radius:50px;
        
        &.active {
          background-color:#000;
          color:#fff;
          border:1px solid transparent;
        }
      }
    }
  }
}

#sparkles > path {
  position: absolute;
  opacity:0;
}

@for $i from 1 through 29 {
	#sparkles > path:nth-child(#{$i}) {
		animation-delay: (35ms * $i);
	}
}

@keyframes sparkles {
  0%, 20% {
    opacity: 0;
  }
  25% {
    opacity: 1;
    box-shadow: 0.32476rem -2.4375rem 0 0rem #ff8080,
    -0.32476rem -2.0625rem 0 0rem #000,
      2.1082rem -1.26585rem 0 0rem #000,
      1.41004rem -1.53985rem 0 0rem #a4ff80,
        2.30412rem 0.85901rem 0 0rem #a4ff80,
        2.08305rem 0.14233rem 0 0rem #80ffc8,
        0.76499rem 2.33702rem 0 0rem #80ffc8,
        1.18748rem 1.71734rem 0 0rem #00022e,
        -1.35019rem 2.0552rem 0 0rem #00022e,
        -0.60229rem 1.99916rem 0 0rem #36013f,
        -2.44865rem 0.22578rem 0 0rem #36013f,
        -1.93852rem 0.77557rem 0 0rem #d90166,
        -1.70323rem -1.77366rem 0 0rem #d90166,
        -1.81501rem -1.03204rem 0 0rem #ff8080;
  }
}