//global elements
* {
  box-sizing:border-box;
}
body {
  overflow-x:hidden;
  font-family: 'Montserrat', sans-serif;
}
h1, .h1, h2 {
  font-family:$mainFont;
  color:#000;

  &.main-heading {
    font-size:26px;
  }
}
a, a:hover,a:visited {
  text-decoration: none;
  color:inherit
}
// classes
#wrapper {
  max-width: 1550px;
  margin:auto;
  position:relative;
  margin-top:40px;
}
.container-fluid {
  padding:0 30px
}
.img-responsive {
  width:100%;
}
.overlay {
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
}

// slick plugin
.slick-dots li button {
  width:10px;
  height:10px;
}
.slick-dots li button::before {
  font-size:4px;
  opacity:1;
  color:$mainColor;
}
.slick-dots li.slick-active button::before {
  color:#000;
}